import { Button, Card, Collapse, Divider, Input, Typography, List } from "antd";
import React, { useEffect, useState } from "react";
import * as WorkspaceAPI from "trimble-connect-workspace-api";
import { ObjectView } from "react-object-view";

const { Text } = Typography;
const SelectionFilter = () => {
  const [fieldValue, setFieldValue] = useState("");
  const [properties, setProperties] = useState([]);
  return (
    <>
      <div
        style={{
          flexDirection: "row",
          flexWrap: "wrap",
          columnGap: "5px",
          rowGap: "5px",
          margin: "2px",
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "row",
            justifyContent: "space-between",
            marginTop: "5px",
            marginLeft: "5px",
            marginRight: "5px",
            columnGap: "2px",
          }}
        >
          <Text style={{ width: "60px" }}>Value</Text>
          <Input
            placeholder="Field Value"
            onChange={(e) => setFieldValue(e.target.value.trim().toUpperCase())}
          />
          <Button
            type="primary"
            onClick={async () => {
              const tcapi = await WorkspaceAPI.connect(window.parent);
              const types = ["IFCBEAM", "IFCMEMBER", "IFCCOLUMN"];
              const objectProperties = [];
              const modelObjectIds = [];

              for (const type of types) {
                const modelObjects = await tcapi.viewer.getObjects({
                  parameter: {
                    class: type,
                  },
                });

                for (const x of modelObjects) {
                  const ids = [];

                  const promises = x.objects.map(async (o) => {
                    try {
                      const properties = await tcapi.viewer.getObjectProperties(
                        x.modelId,
                        [o.id]
                      );
                      let id = `${x.modelId}${o.id}`;
                      const modelId = x.modelId;
                      let profile = properties[0].product.description;
                      let name = properties[0].product.name;
                      let material = "";
                      let partPos = "";
                      let asmPos = "";
                      let partClass = "";
                      let weight = 0;
                      let length = 0;

                      properties[0].properties.forEach((prop) => {
                        prop.properties.forEach((a) => {
                          if (
                            a.name.toUpperCase().includes("NAME") &&
                            name === ""
                          )
                            profile = a.value;
                          if (
                            a.name.toUpperCase().includes("PROFILE") &&
                            profile === ""
                          )
                            profile = a.value;
                          if (
                            a.name.toUpperCase().includes("MATERIAL") &&
                            material === ""
                          )
                            material = a.value;
                          if (
                            a.name.toUpperCase().includes("WEIGHT") &&
                            weight === 0
                          )
                            weight = Math.round(Number(a.value) * 100) / 100;
                          if (
                            a.name.toUpperCase().includes("LENGTH") &&
                            length === 0
                          )
                            length = Math.round(Number(a.value));
                          if (
                            a.name.toUpperCase().includes("REFERENCE") &&
                            partPos === ""
                          )
                            partPos = a.value;
                          if (
                            a.name.toUpperCase().includes("PART_POS") &&
                            partPos === ""
                          )
                            partPos = a.value;
                          if (
                            a.name.toUpperCase().includes("ASSEMBLY_POS") &&
                            asmPos === ""
                          )
                            asmPos = a.value;
                          if (
                            a.name.toUpperCase().includes("CLASS_ATTR") &&
                            partClass === ""
                          )
                            partClass = a.value;
                          if (
                            a.name.toUpperCase().includes("CLASS") &&
                            partClass === ""
                          )
                            partClass = a.value;
                        });
                      });

                      if (
                        profile.toUpperCase().includes(fieldValue) ||
                        name.toUpperCase().includes(fieldValue) ||
                        material.toUpperCase().includes(fieldValue) ||
                        partPos.toUpperCase().includes(fieldValue) ||
                        asmPos.toUpperCase().includes(fieldValue) ||
                        partClass.toUpperCase().includes(fieldValue)
                      ) {
                        const objId = o.id;
                        ids.push(objId);
                        const items = [
                          { title: "Name", value: name },
                          { title: "Profile", value: profile },
                          { title: "Material", value: material },
                          { title: "Class", value: partClass },
                          { title: "Part Position", value: partPos },
                          { title: "Assembly Position", value: asmPos },
                          { title: "Weight", value: weight },
                          { title: "Length", value: length },
                        ];
                        objectProperties.push({
                          modelId: modelId,
                          objId: objId,
                          key: id,
                          label: name,
                          children: (
                            <List
                              size="small"
                              bordered
                              dataSource={items}
                              renderItem={(item) => (
                                <List.Item
                                  style={{
                                    display: "flex",
                                    justifyContent: "space-between",
                                  }}
                                >
                                  <List.Item.Meta
                                    title={item.title}
                                    description={item.value}
                                  />
                                </List.Item>
                              )}
                            />
                          ),
                        });
                      }
                    } catch (error) {
                      console.error(
                        `Error fetching properties for object ${o.id}:`,
                        error
                      );
                    }
                  });

                  await Promise.all(promises); // Chờ tất cả các Promise hoàn thành
                  modelObjectIds.push({
                    modelId: x.modelId,
                    objectRuntimeIds: ids,
                  });
                }
              }

              await tcapi.viewer.setSelection(
                {
                  modelObjectIds: modelObjectIds,
                },
                "set"
              );
              setProperties(objectProperties);
            }}
          >
            Select
          </Button>
        </div>
        <Divider orientation="left">Object Properties</Divider>

        <Collapse items={properties} />
      </div>
    </>
  );
};

export default SelectionFilter;
